export default {
	data() {
		return {
			token:'',//onShow时获取token存起来，以便每次发送请求都要重新获取
			rebateMoney:'',
			name:'',
			phone:'',
		}
	},
	onShow(){
		this.token = uni.getStorageSync('token')
		console.log(this.token)
		if(!this.token){
			uni.navigateTo({
				url: '../login/login'
			})
			return 
		}
		uni.request({
			url: this.url + '/mobile/realData',
			method:'GET',
			header: {
				'content-type': 'application/x-www-form-urlencoded' ,// 默认值
				'token': this.token
			},
			data:{
				
			},
			success: (res) => {
				console.log(res);
				this.rebateMoney = res.data.rebateMoney+''
				this.name = res.data.name+''
				this.phone = res.data.tel+''
			}
		})
	},
	methods: {
		goCallUs(){
			uni.navigateTo({
				url: '../callUs/callUs'
			})
		},
		goCompany_recovery(){//跳转企业回收页面
			uni.navigateTo({
				url: '../company_recovery/company_recovery'
			})
		},
		goFind_shoper(){//跳转招商合作页面
			uni.navigateTo({
				url: '../find_shoper/find_shoper'
			})
		},
		goProblem_often(){//跳转常见问题页面
			uni.navigateTo({
				url: '../problem_often/problem_often'
			})
		},
		goInvite(){
			uni.navigateTo({
				url: '../invite/invite'
			});
		},
		goUserInfo(){//跳转个人信息页面
			uni.navigateTo({
				url: '../userInfo/userInfo'
			});
		},
		goAccount(){//跳转打款账户页面
			uni.navigateTo({
				url: '../account/account'
			});
		},
		goCommission(){//跳转佣金记录页面
			uni.navigateTo({
				url: '../commission/commission'
			});
		},
		goFeedback(){//跳转意见反馈页面
			uni.navigateTo({
				url: '../feedback/feedback'
			});
		},
		goRecord(){//跳转提现记录页面
			uni.navigateTo({
				url: '../record/record'
			});
		},
		goWithdrawDeposit(){//跳转体现页面
			uni.navigateTo({
				url: '../withdrawDeposit/withdrawDeposit'
			});
		}
	}
}
